import Debugger from "./debugger";
export default class Executor {
    constructor(context, options = {
        useHostWindow: false,
    }) {
        this.context = context;
        this.options = options;
        this.debugger = new Debugger();
        this.resume = this.debugger.resume.bind(this.debugger);
        this.panic = this.debugger.panic.bind(this.debugger);
        this.destroy = this.debugger.destroy.bind(this.debugger);
    }
    async execute(code) {
        let output;
        const iframe = document.createElement("iframe");
        iframe.style.display = "none";
        document.body.appendChild(iframe);
        const iframeWindow = this.options.useHostWindow
            ? window
            : iframe.contentWindow;
        const createFunction = iframeWindow.Function;
        this.context["debug"] = {
            wait: this.debugger.wait.bind(this.debugger),
        };
        Object.entries(this.context).forEach(([key, value]) => (iframeWindow[key] = value));
        const fn = createFunction(`return (async () => {${code}})()`);
        output = await fn();
        document.body.removeChild(iframe);
        return output;
    }
}
